.error {
  color: red;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 0.3em;
  padding: 1em;
  margin: 1em;
}

.success {
  color: green;
  background: lightgrey;
  font-size: 20px;
  border-style: solid;
  border-radius: 0.3em;
  padding: 1em;
  margin: 1em;
}
